import {Piloto} from '../models/Piloto';

export class PilotoDao {

    constructor(connection) {

        this._connection = connection;
        this._store = 'pilotos';
    }

    adiciona(piloto) {
         return new Promise((resolve, reject) => {
            let request = this._connection
                .transaction([this._store], 'readwrite')
                .objectStore(this._store)
                .add(piloto);

            request.onsuccess = e => {
                resolve();
            };

            request.onerror = e => {
                console.log(e.target.error);
                reject('Não foi possível adicionar o piloto!');
            };
        });
    }

    listaTodos() {
        return new Promise((resolve, reject) => {
            let cursor = this._connection
                .transaction([this._store], 'readwrite')
                .objectStore(this._store)
                .openCursor();

            let pilotos = [];

            cursor.onsuccess = e => {
                let atual = e.target.result;

                if(atual) {
                    let dado = atual.value;
                    pilotos.push(new Piloto(dado._codigo, dado._nome));
                    atual.continue();
                } else {
                    resolve(pilotos);
                }
            };

            cursor.onerror = e => {
                console.log(e.target.error);
                reject('Não foi possível listar os pilotos!');
            };
        });
    }

    apagaTodos() {
        return new Promise((resolve, reject) => {

            let request = this._connection
                .transaction([this._store], 'readwrite')
                .objectStore(this._store)
                .clear();

            request.onsuccess = e => resolve('Pilotos apagados com sucesso!');

            request.onerror = e => {
                console.log(e.target.error);
                reject('Não foi possível apagar os pilotos!');
            }; 
        });
    }
}
